import React, { Component } from 'react';

class Calculator extends Component {
  constructor(props) {
    super(props);

    this.state = {
      num1: '',
      num2: '',
      result: 0
    };

    this.setNum1 = this.setNum1.bind(this);
    this.setNum2 = this.setNum2.bind(this);
    this.add = this.add.bind(this);
    this.subtract = this.subtract.bind(this);
    this.multiply = this.multiply.bind(this);
    this.divide = this.divide.bind(this);
    this.clear = this.clear.bind(this);

  }

  setNum1(e) {
    const num1 = e.target.value ? parseInt(e.target.value) : "";
    this.setState({ num1 });
  }

  setNum2(e) {
    const num2 = e.target.value ? parseInt(e.target.value) : "";
    this.setState({num2});
  }

  add(e) {
    e.preventDefault();
    const sum = (this.state.num1) + (this.state.num2);
    this.setState({result: sum});
  }

  subtract(e) {
    e.preventDefault();
    const sub = (this.state.num1) - (this.state.num2);
    this.setState({result: sub});
  }

  multiply(e) {
    e.preventDefault();
    const product = (this.state.num1) * (this.state.num2);
    this.setState({result: product});
  }

  divide(e) {
    e.preventDefault();
    const quotient = (this.state.num1) / (this.state.num2);
    this.setState({result: quotient});
  }

  clear(e) {
    e.preventDefault();
    this.setState({num1: ""});
    this.setState({num2: ""});
    this.setState({result: 0});
  }

  render(){
    // const {num1, num2, result} = this.state
    const {num1, num2, result} = this.state
    return (
      <div className='calc'>
        <h1 className='result'>{result}</h1>
        <input onChange={this.setNum1} value={num1} />
        <input onChange={this.setNum2} value={num2} />
        <button value="+" onClick={this.add} > + </button>
        <button value="-" onClick={this.subtract}> - </button>
        <button value="*" onClick={this.multiply}> * </button>
        <button value="/" onClick={this.divide}> / </button>
        <button onClick={this.clear} > Clear </button>
      </div>
    );
  }

}


export default Calculator;
